export default {
	onLoad() {
		this.initMyInfo()
	},
	onShow() {
		this.initMyInfo()
	},
	data () {
		return {
			
			totalPrice: ''
		}
	},
	onPullDownRefresh () {
		this.initMyInfo()
		uni.stopPullDownRefresh()
	},
	methods: {
		//跳转充值
		goToRecharge () {
			uni.navigateTo({
				url: './recharge'
			})
		},
		//账单明细
		goTobillDetail () {
			uni.navigateTo({
				url: './billDetail'
			})
		},
		//开票管理
		goToInvioce () {
			uni.navigateTo({
				url: 'invioce'
			})
		},
		//到优惠券
		goToDiscount () {
			uni.navigateTo({
				url:'../discount/discount?status=' + 0
			})
		},
		//到充值优惠券
		goToRechargeDiscount () {
			uni.navigateTo({
				url:'../rechargeDiscount/rechargeDiscount'
			})
		},
		async initMyInfo () {

				let res = await this.$fetch(this.$api.currentUser,{},"GET","form")
				console.log(res)
				if (res.code === 0) {
					
					this.totalPrice = res.data.amount.toFixed(2)
				
				} else {
					uni.showToast({
						icon:'none',
						title:res.msg
					})
				}
		},
	}
}
